

.nav{
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 20px;

    background-color: rgb(112, 174, 255);
    max-width: 1100px;
    margin: 0 auto;
    padding: 20px;
}

.pageBox{
    position: relative;
    filter: grayscale(50%);
    transition: .2s;
}
.pageBox:hover{
    filter: grayscale(0);
    cursor: pointer;
    outline: 5px solid rgba(0, 0, 0, 0.616);
    z-index: 1;
}
.pageTitle{
    color: #000;
    text-shadow: 0 0 2px rgb(255, 255, 255);
    background-color: rgba(255, 255, 255, 0.5);
    width: 100%;
    padding: 0;
    margin: 0;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}
.pageImg{
    width: 100%;
    height: 100%;
}

@media(max-width:1000px){
    .pageTitle{ font-size: 15px; }
}
@media(max-width: 700px){
    .nav{
        box-sizing: border-box;
        width:100%;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(5, 1fr);
    }
    .pageTitle{
        font-size: 25px;
    }
}