
:root{
    --imgSize:90px;
    --textSize:20px;
}


/* MAIN SECTION */
.main{
    margin: 0 auto;
    width: 90%;
    border: 1px solid black;
    display: flex;
    align-items: flex-start;
}

/* MAKROS */
.makros{
    width: 300px;
    background-color: rgb(255, 220, 220);
    flex-shrink: 0;
    
}
.porActiv{
    outline: 1px solid black;
    cursor: pointer;
}
.porNotActiv{
    cursor: pointer;
}

.div-li{
    display: flex;
    margin: 10px 0;
    align-items: center;
}
.ing-ol{
    list-style: none;
    padding-left: 0;
    text-align: start;
}
.ing-li{
    font-size: var(--textSize);
}

.img-li{
    border: 2px solid black;
    display: inline-block;
    width: var(--imgSize);
    height: var(--imgSize);
    border-radius: 50%;
}
.makrosDiv{
    overflow: hidden;
    transition: 1s;
}
.makroSwitch{
    margin-top: 15px;
    border: 4px solid #345;
    border-radius: 99px;

}
.makroSwitch:hover{
    cursor: pointer;
}

.mSV{
    writing-mode: vertical-lr;
    text-orientation: upright;
    position: sticky;
    top: 50px;   
}
.makroSwitchVerticalEx::after{
    content: "Expand";
    position: absolute;
    padding: 10px;
    border: 3px solid rgb(0, 58, 17);
    border-radius: 5px;
}
.makroSwitchVerticalEx:hover::after,
.makroSwitchVerticalCol:hover::after{
    cursor: pointer;
}
.makroSwitchVerticalCol::after{
    content: "Collapse";
    position: absolute;
    padding: 10px;
    border: 3px solid rgb(0, 58, 17);
    border-radius: 5px;
}


/* INFO SECTION



/* SEARCH TAGS */
.searchTags{
    width: 500px;
    background-color: rgb(197, 239, 255);

}
.foodCat, .healthLabel{
    display: flex;
    flex-wrap: wrap;
}
.foodCat p, .healthLabel p{
    margin: 5px;
    padding: 5px;
    border: 2px solid black;
    border-radius: 15px;
    transition: .2s;

}
.foodCat p:hover, .healthLabel p:hover{
    cursor: pointer;
    border-radius: 5px;
}


@media(max-width:1200px){
    .main{
        display: grid;
        grid-template-columns: 300px 1fr;
    }
    .searchTags{
        width: 100%;
        grid-column: 1/span 2;
    }

}
@media(max-width:700px){
    .main{
        display: flex;
        flex-direction: column;
    }
}
