@import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');

*{
  font-family: 'Lato', sans-serif;
}
html{
  scroll-behavior: smooth;
}
body{
  margin: 0;
}

.App {
  text-align: center;
}

.search-bar{
  border: 0;
  font-size: 25px;
  padding: 0px 15px 1px 10px;
  border-bottom: 3px solid black;
  background-color: transparent;
  transition: .3s
}

.search-bar:focus{
  outline: none;
  border-bottom: 3px solid rgb(49, 151, 80);
}

.search-button{
  font-size: 25px;
  background: none;
  outline: none;
  /* border: none; */
  border: 3px solid black;
}
.search-button:hover{
  cursor: pointer;
}